
html,body{
    font-family: 'Lora', serif !important;
    overflow-x: hidden;
}
.backgroundCover {
    /* The image used */
    /*    background-image: url("./imgs/hero_1.JPG");*/
    
    /* Full height */
    height: 100vh;

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    position: relative;
}

.homeLogo{
    position:absolute;
    left:50%;
    top:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%); 
}

/* Laptop and Up */
@media (min-width: 1025px){
    .homeLogo{
        max-width: 45%;
    }
}

/* Tablets */
@media only screen and (max-width: 1024px){
    .homeLogo{
        max-width: 55%;
    }
}

/* Phones */
@media only screen and (max-width: 767px){
    .homeLogo{
        max-width: 75%;
    }
}
.Footer{
    background-color:#1a1a1a;
    font-family: 'Mukta Mahee', sans-serif;
    line-height: 1.8;
    color: rgba(255, 255, 255, 0.5);
    padding-top: 4%;
    padding-bottom: 2%;

}

.title{
    color: white;
}
.MainChoicePanel{
    padding-top: 6%;
    padding-bottom: 6%;
}

.card{
	margin-top:2%; 

}
.MainVideo{
    /*background-image: url("https://github.com/ehalekote/BalurEstate/blob/master/img/round.png?raw=true");*/
    background-repeat: repeat;

    text-align: center;

